import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminRolesListComponent } from './admin-roles-list/admin-roles-list.component';
import { NewRoleListComponent } from './admin-roles-list/new-role-list/new-role-list.component';

// const routes: Routes = [
// {
//   path:"",
//   component:AdminRolesListComponent
// }


// ];
const routes: Routes = [
  {
    path: '',
    component: NewRoleListComponent,
    children: [
      {
        path: '',
        redirectTo: 'role_product',
        pathMatch: 'full'
      },
      {
        path: "role_product",
        component: AdminRolesListComponent
      }
    ]
  },
]


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminNewModuleRoutingModule { }
